import logo from './logo.svg';
import React, {useState, useEffect} from "react";
import axios from "axios";
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

function App() {
    let [liveEvents, setLiveEvents] = useState([])
    useEffect(() => {
        axios({
            method: "get",
            url: '/api/live_events/',
        }).then(response => {
            setLiveEvents(response.data);
        })
    })
    console.log(liveEvents)
    let baseFactors = {
        win1: '1',
        draw: 'X',
        win2: '2',
        win1draw: '1X',
        win2draw: 'X2',
        notDraw: '12',
        totalOver: 'ТБ',
        totalUnder: 'ТМ',
        other: 'Всего'

    }


    return (
        <div className="wrapper">
            <h1>Текушие события:</h1>
            <div className="table">
                <div className="table-header">
                    <div className="line">
                        <div className="event-name">Событий найдено: {liveEvents.length}</div>
                        <div className="counters">
                            {Object.keys(baseFactors).map(key =>(
                                <div className="counter">{baseFactors.get(key)}</div>
                            ))}

                            {/*<div className="counter">X</div>*/}
                            {/*<div className="counter">2</div>*/}
                            {/*<div className="counter">1X</div>*/}
                            {/*<div className="counter">12</div>*/}
                            {/*<div className="counter">X2</div>*/}
                            {/*<div className="counter">Ф1</div>*/}
                            {/*<div className="counter">Ф2</div>*/}
                            {/*<div className="counter">ТБ</div>*/}
                            {/*<div className="counter">ТМ</div>*/}
                            {/*<div className="counter">Всего</div>*/}
                        </div>
                    </div>
                </div>
                <div className="table-body">
                    {liveEvents.map(event => (
                        <div className="line">
                            <div className="event-name" key={event.id}>{event.name}</div>
                            <div className="counters">
                                {event.counters.map(c => (
                                    <div className="counter">{c.value}</div>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default App;
